header {
    height: 15vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.empty {
    flex: 1 1;
    color: white;
}

.logo {
    flex: 1.5 1;
    text-align: center;
    width: 220rem;
    margin-left: auto;
}

.topLevel {
    color: black;
}

.bottomLevel {
    color: black;
}

nav {
    margin: 0 auto;
    flex: 1 1;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
  color: white;  
}

.nav-links {
    width: 100%;
    display: flex;
    color: black;
    justify-content: space-around;
}

.nav-links li {
    font-size: 2rem;
    color: black;
}

.category-nav-outer {
    width: 100%;
    margin: 0 auto;
    box-shadow: 0 0 0 0.3rem rgba(167, 166, 166, 0.158);
}

.category-nav {
    display: flex;
    width: 60%;
    align-items: center;
    height: 6vh;
    margin: 0 auto;
}

.category-nav-links {
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.category-nav-links li {
    font-size: 2rem;
    font-weight: 300;
    text-decoration: none;
    list-style: none;
}

@media screen and (max-width: 1200px) {
    .category-nav {
        width: 80%;
    }
}

@media screen and (max-width: 800px) {
    .category-nav {
        width: 100%;
    }
}
footer {
    padding-top: 2vh;
    padding-bottom: 2vh;
    width: 100%;
    background-color: rgb(55, 55, 70);
    position: absolute;
    bottom: 0
}

nav {
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
  color: white;  
}

.footer-links {
    display: flex;
