* {
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    box-sizing: border-box;
    font-family: "Times New Roman", Times, serif;
}

html {
    font-size: 62.5%;
    position: relative;
}

.big-header {
    font-size: 12rem;
}

h1 {
    font-size: 8rem;
}

h2 {
    font-size: 5.5rem;
}

h3 {
    font-size: 3rem;
}

h4 {
    font-size: 1.5rem;
}

p {
    font-size: 2.2rem;
    line-height: 4rem;
    font-family: 'Libre Caslon Text', serif;
}

.button {
    cursor: pointer;
    padding: 1.5rem 3rem;
    background-color: white;
    color: black;
    font-size: 2.5rem;
    text-decoration: none;
    margin-right: 2rem;
    font-weight: bold;
    box-shadow: 0.3rem 0.5rem rgba(104, 104, 104, 0.192);
}

@media screen and (max-width: 1400px) {
    html {
        font-size: 55%;
    }
}

@media screen and (max-width: 1050px) {
    html {
        font-size: 50%;
    }
}
@media screen and (max-width: 700px) {
    html {
        font-size: 45%;
    }
}

@media screen and (max-width: 550px) {
    html {
        font-size: 40%;
    }
}


@media screen and (max-width: 550px) {
    html {
        font-size: 35%;
    }
}

